@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  width: 100%;
  height: 100vh;
  font-family: Karla, sans-serif;
  background: #222;
  background: var(--black);
  color: #d3d3d3;
  color: var(--white);
  --yellow: rgb(255, 244, 137);
  --pink: rgb(250, 87, 193);
  --purple: rgb(177, 102, 204);
  --dark-puple: rgb(117, 114, 255);
  --blue: rgb(105, 166, 249);
  --black: #222;
  --white: #d3d3d3;
  display: block;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  width: 50%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.infograph {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: flex-start;
}

.dynamic {
  display: grid;
  width: 65%;
  grid-template-columns: repeat(3, 1fr);
}
.list {
  display: flex;
}
.list li {
  margin: 0 1rem;
  list-style: none;
}

.list li:nth-child(1) {
  color: #375e92;
}
.list li:nth-child(2) {
  color: #fb6542;
}
.list li:nth-child(3) {
  color: #ffbb00;
}

@media all and (max-width: 768px) {
  .container {
    width: 100%;
    height: 100%;
  }
  .infograph{
    margin: 2rem 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .dynamic{
    grid-template-columns: repeat(1, 1fr);
  }
}
.graph {
  width: 60%;
  height: 50%;
  display: flex;
}
input {
  outline:none;
  width: 100%;
  height: 2.5rem;
  padding-left: .3rem;
  border: 2px solid rgb(255, 244, 137);
  font-size: 1rem;
}
.stats {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  margin-top: 8%;
  position: relative;
}

.stats h3 {
  margin: 0.5rem;
}

.stats ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.stats ul li {
  margin: .5rem 0;
  transform: translateX(-20%);
  -webkit-animation: test .4s linear .2s forwards;
          animation: test .4s linear .2s forwards;
}

@-webkit-keyframes test{
  0% {}
  100% {transform: translateX(10%);}
}

@keyframes test{
  0% {}
  100% {transform: translateX(10%);}
}
.stats ul li span {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: inline-block;
  margin-right: .2rem;
}

.stats ul li:nth-child(1) span {
  background-color: rgb(255, 244, 137);
}
.stats ul li:nth-child(2) span {
  background-color: rgb(250, 87, 193);
}
.stats ul li:nth-child(3) span {
  background-color: rgb(177, 102, 204);
}
.stats ul li:nth-child(4) span {
  background-color: rgb(117, 114, 255);
}
.stats ul li:nth-child(5) span {
  background-color: rgb(105, 166, 249);
}

@media all and (max-width: 768px) {
  .stats {
    width: 70%;
    margin-right: 10%;
  }
}

.loader {
  border: 2.5px solid rgba(255,255,255,.2);
  border-radius: 50%;
  border-top: 2.5px solid rgb(255, 255, 255, .8);
  width: 28px;
  height: 28px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.total {
  width: 60%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
